"use client";

import React, { useState, useMemo } from 'react';
import { Video, VideoOff, Mic, MicOff } from 'lucide-react'; // Import icons for mute/unmute
import { Card, CardContent, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { CameraFeed } from './camera-feed'; 
import { Separator } from '@/components/ui/separator';
import { AnnotationToolbar } from './annotation-toolbar';

const url: string = "/video.mp4";
const ALL_CAMERAS_DATA = [
  { id: 'cam1', name: 'Living Room Cam', posterUrl: url, dataAiHint: 'living room pet' },
  { id: 'cam2', name: 'Kitchen Cam', posterUrl: url, dataAiHint: 'kitchen pet' },
  { id: 'cam3', name: 'Play Area Cam', posterUrl: url, dataAiHint: 'playroom pet' },
  { id: 'cam4', name: 'Backyard Cam', posterUrl: url, dataAiHint: 'backyard garden' },
];

export function VideoPlayer() {
  const [selectedCameraIds, setSelectedCameraIds] = useState<string[]>(ALL_CAMERAS_DATA.map(c => c.id));
  const [isMuted, setIsMuted] = useState(false); // State for mute/unmute
  const [mainCameraId, setMainCameraId] = useState<string>(ALL_CAMERAS_DATA[0].id); // Track the main feed camera

  const handleToggleCamera = (cameraId: string) => {
    if (cameraId === mainCameraId) return; // Don't do anything if the selected camera is already the main feed

    // Swap the camera feed
    setMainCameraId(cameraId); // Update the main feed
  };

  const displayedCameras = useMemo(() => {
    return ALL_CAMERAS_DATA.filter(cam => selectedCameraIds.includes(cam.id));
  }, [selectedCameraIds]);

  const toggleMute = () => {
    setIsMuted(prev => !prev);
  };

  return (
    <Card className="shadow-lg w-[80%] p-4">
      <CardHeader>
        <CardTitle className="flex items-center gap-2 text-xl font-headline">
          <Video className="h-6 w-6 text-primary" />
          Multi-Camera View
        </CardTitle>
      </CardHeader>
      <CardContent>
        <div className="grid grid-cols-4 grid-rows-7 gap-4">
          {/* Main Camera Feed */}
          {displayedCameras.length > 0 && (
            <div className="col-span-3 row-span-5">
              <CameraFeed
                key={mainCameraId} // Ensure this updates when the camera feed changes
                id={mainCameraId}
                name={displayedCameras.find(cam => cam.id === mainCameraId)?.name || ''}
                posterUrl={displayedCameras.find(cam => cam.id === mainCameraId)?.posterUrl || url}
                dataAiHint={displayedCameras.find(cam => cam.id === mainCameraId)?.dataAiHint || ''}
              />
            </div>
          )}

          {/* Additional Camera Feeds */}
          <div className="row-span-2 col-start-1 row-start-6">
            {displayedCameras.length > 1 && displayedCameras[0].id !== mainCameraId && (
              <CameraFeed
                key={displayedCameras[0].id} // Ensure correct key to force render when swapping
                id={displayedCameras[0].id}
                name={displayedCameras[0].name}
                posterUrl={displayedCameras[0].posterUrl}
                dataAiHint={displayedCameras[0].dataAiHint}
              />
            )}
          </div>
          <div className="row-span-2 col-start-2 row-start-6">
            {displayedCameras.length > 2 && displayedCameras[1].id !== mainCameraId && (
              <CameraFeed
                key={displayedCameras[1].id} // Ensure correct key to force render when swapping
                id={displayedCameras[1].id}
                name={displayedCameras[1].name}
                posterUrl={displayedCameras[1].posterUrl}
                dataAiHint={displayedCameras[1].dataAiHint}
              />
            )}
          </div>
          <div className="row-span-2 col-start-3 row-start-6">
            {displayedCameras.length > 3 && displayedCameras[2].id !== mainCameraId && (
              <CameraFeed
                key={displayedCameras[2].id} // Ensure correct key to force render when swapping
                id={displayedCameras[2].id}
                name={displayedCameras[2].name}
                posterUrl={displayedCameras[2].posterUrl}
                dataAiHint={displayedCameras[2].dataAiHint}
              />
            )}
          </div>

          {/* Logo */}
          <div className="col-start-4 row-start-1">
            <img src="/logo.png" alt="Logo" className="h-16" />
          </div>

          {/* Mute/Unmute Button */}
          <div className="col-start-4 row-start-2">
            <Button onClick={toggleMute} variant="outline" size="sm">
              {isMuted ? <MicOff className="h-4 w-4" /> : <Mic className="h-4 w-4" />}
            </Button>
          </div>

          {/* Camera Selection */}
          <div className="row-span-5 col-start-4 row-start-3">
            <CardFooter className="flex flex-row sm:flex-col items-start sm:items-center gap-4 pt-4 border-t">
              <div className="font-medium text-sm">Select Cameras:</div>
              <Separator orientation="horizontal" className="h-[1px] hidden sm:block" />
              <div className="flex flex-wrap gap-2">
                {ALL_CAMERAS_DATA.map(camera => (
                  <Button
                    key={camera.id}
                    variant={selectedCameraIds.includes(camera.id) ? 'secondary' : 'outline'}
                    size="sm"
                    onClick={() => handleToggleCamera(camera.id)}
                    className={selectedCameraIds.includes(camera.id) ? 'bg-primary/20 border-primary text-primary-foreground hover:bg-primary/30' : ''}
                  >
                    {camera.name.replace(' Cam', '')}
                  </Button>
                ))}
              </div>
              <AnnotationToolbar />
            </CardFooter>
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
